import { Typography } from "@mui/material";
import { Box } from "@mui/system";
import "./header.scss";

const Header = () => {
  return (
    <div className="header" id="home">
      <div className="header-container">
        {/* <span>
          <div className="purple">marins</div>arquitetura
        </span> */}
        <Box sx={{ display: "flex", gap: 1, fontSize: "26px" }}>
          <Typography color="secondary.main">marins</Typography>
          <Typography color="primary.light">arquitetura</Typography>
        </Box>
        <span>{new Date().getFullYear()}</span>
      </div>
    </div>
  );
};

export default Header;
